<template>
  <div>
    <el-dialog :title="info.title" :visible.sync="info.show">
      <el-form :model="form">
        <el-form-item label="活动名称" :label-width="formLabelWidth">
          <el-input v-model="form.title"></el-input>
        </el-form-item>
        <!--日期 -->

        <el-form-item class="block" label="活动期限" :label-width="formLabelWidth">
          <div class="block">
            <el-date-picker
              v-model="value"
              type="datetimerange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              value-format="timestamp"
            ></el-date-picker>
          </div>
        </el-form-item>
        <!-- 联动效果 -->
        <el-form-item label="一级分类" :label-width="formLabelWidth">
          <!-- @change 是element UI库的方法：选中值发生变化时触发 -->
          <el-select v-model="form.first_cateid" @change="changeCate(false)">
            <el-option label="--请选择---" value disabled></el-option>
            <!-- 遍历一级分类 -->
            <el-option
              v-for="item in cateList"
              :key="item.id"
              :label="item.catename"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="二级分类" :label-width="formLabelWidth">
          <el-select v-model="form.second_cateid">
            <el-option label="--请选择---" value disabled></el-option>
            <!-- 遍历二级分类 -->
            <el-option
              v-for="item in secondCate"
              :key="item.id"
              :label="item.catename"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="商品" :label-width="formLabelWidth">
          <el-select v-model="form.goodsid">
            <el-option label="--请选择---" value disabled></el-option>
            <el-option
              v-for="item in goodsList"
              :key="item.id"
              :label="item.goodsname"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- 状态 -->
        <el-form-item label="状态" :label-width="formLabelWidth">
          <el-switch
            v-model="form.status"
            active-color="skyblue"
            inactive-color="#ff4949"
            :active-value="1"
            :inactive-value="2"
          ></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="confirm" v-if="info.isAdd">确 定</el-button>
        <el-button type="primary" @click="update" v-else>修改</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { successAlert } from "../../../utils/alerts";
import { addSeck, getOneSeck, updateSeck } from "../../../utils/req";
export default {
  props: ["info"],
  data() {
    return {
      formLabelWidth: "120px",
      value: [],
      secondCate: "",
      form: {
        title: "",
        begintime: "",
        endtime: "",
        first_cateid: "",
        second_cateid: "",
        goodsid: "",
        status: 1,
      },
    };
  },
  computed: {
    ...mapGetters({
      cateList: "cate/cateList",
      goodsList: "goods/goodsList",
      seckList: "seckill/seckList",
    }),
  },
  methods: {
    ...mapActions({
      reqCateList: "cate/cateListActions",
      reqGoodsList: "goods/goodsListActions",
      reqSeckList: "seckill/seckListActions",
    }),
    changeCate(boo) {
      if (!boo) {
        this.form.second_cateid = "";
      }
      let index = this.cateList.findIndex(
        (item) => item.id == this.form.first_cateid
      );
      this.secondCate = this.cateList[index].children;
    },

    // 点击取消时
    cancel() {
      this.info.show = false;
      this.value = [];
      this.form = {
        title: "",
        begintime: "",
        endtime: "",
        first_cateid: "",
        second_cateid: "",
        goodsid: "",
        status: 1,
      };
    },
    // 点击确定的时候
    confirm() {
      this.form.begintime = this.value[0];
      this.form.endtime = this.value[1];
      addSeck(this.form).then((res) => {
        successAlert(res.data.msg);
        this.cancel();
        this.reqSeckList();
      });
    },
    // 获取要编辑的秒杀活动的信息
    getDetail(id) {
      getOneSeck({ id }).then((res) => {
        this.value.push(this.form.begintime) ;
        this.value.push(this.form.endtime);
        this.form = res.data.list;
        this.form.id = id;
        this.changeCate(true); //获取二级分类
      });
    },
    update() {
      updateSeck(this.form).then((res) => {
        successAlert(res.data.msg);
        this.cancel();
        this.reqSeckList();
      });
    },
  },
  mounted() {
    this.reqCateList();
    this.reqGoodsList();
  },
};
</script>

<style>
</style>


